import moment from '@/components/moment';
import EiCalendar from '@/components/ei-calendar/ei-calendar';
export default {
	onShareAppMessage() {},
	components: {
		EiCalendar
	},
	data() {
		return {
			sum_data: { session_s: '----', total_bo_s: '-------' },
			res: {},
			date: [],
			time_tab_Br: false,
			customDate: [],
			index: 0,
			is_index: true,
			itemlist: [],
			time: '',
			zb_title: [
				{
					zb_name: '票房',
					two_title: '(万元)',
					is_check: '',
					no_check: '分账',
					front_row: true
				},
				{
					zb_name: '票房',
					two_title: '份额',
					is_check: '',
					no_check: '分账',
					front_row: true
				},
				{
					zb_name: '排场份额',
					two_title: '',
					is_check: '',
					no_check: '',
					front_row: true
				},
				// {
				// 	zb_name: '上座率',
				// 	two_title: '',
				// 	is_check: '',
				// 	no_check: '',
				// 	front_row: true
				// },
				{
					zb_name: '排座占比',
					two_title: '',
					is_check: '',
					no_check: '',
					front_row: true
				},
				{
					zb_name: '票价',
					two_title: '',
					is_check: '平均',
					no_check: '平均',
					front_row: false
				},
				{
					zb_name: '场均人次',
					two_title: '',
					is_check: '',
					no_check: '',
					front_row: false
				},
				{
					zb_name: '场次',
					two_title: '',
					is_check: '',
					no_check: '',
					front_row: false
				},

				{
					zb_name: '人次',
					two_title: '',
					is_check: '',
					no_check: '',
					front_row: false
				},
				{
					zb_name: ' ',
					two_title: '',
					is_check: '',
					no_check: '',
					front_row: false
				}
			]
		};
	},
	filters: {
		// 格式化周几
		time_zhou(data) {
			// console.log(data)
			var time = '周' + '日一二三四五六'.charAt(new Date(data).getDay());
			if (data == undefined) {
				return '---';
			} else if (data == null) {
				return '---';
			} else {
				return time;
			}
		},
		// 格式化时间
		time_com(data) {
			// console.log(data)
			var time = moment(data).format('YYYY年MM月DD日');
			if (data == undefined) {
				return '---';
			} else if (data == null) {
				return '---';
			} else {
				return time;
			}
		}
	},
	onReady() {
		var a = new Date();
		a = moment(a).format('YYYY-MM-DD');
		var date = [];
		date[0] = new Date(a);
		this.date = date;
	},
	onLoad(res) {
		// mid 我们的id
		// id 专资id
		var { id, mid, index ,city,brd} = res;
		// 初始化页面时间
		var time = moment(new Date()).format('YYYY-MM-DD');
		this.time = time;
		var url = '';
		let All_data = {};
		var zb_title = [
			{
				zb_name: '票房',
				two_title: '(万元)',
				is_check: '',
				no_check: '分账',
				front_row: true
			},
			{
				zb_name: '票房',
				two_title: '份额',
				is_check: '',
				no_check: '分账',
				front_row: true
			},
			{
				zb_name: '排场份额',
				two_title: '',
				is_check: '',
				no_check: '',
				front_row: true
			},
			// {
			// 	zb_name: '上座率',
			// 	two_title: '',
			// 	is_check: '',
			// 	no_check: '',
			// 	front_row: true
			// },
			// {
			// 	zb_name: '排座占比',
			// 	two_title: '',
			// 	is_check: '',
			// 	no_check: '',
			// 	front_row: true
			// },
			{
				zb_name: '票价',
				two_title: '',
				is_check: '平均',
				no_check: '平均',
				front_row: true
			},
			{
				zb_name: '场均人次',
				two_title: '',
				is_check: '',
				no_check: '',
				front_row: false
			},
			{
				zb_name: '场次',
				two_title: '',
				is_check: '',
				no_check: '',
				front_row: false
			},

			{
				zb_name: '人次',
				two_title: '',
				is_check: '',
				no_check: '',
				front_row: false
			},
			{
				zb_name: ' ',
				two_title: '',
				is_check: '',
				no_check: '',
				front_row: false
			}
		];
			url = '/topprogram/movie-maincinema';
			All_data = {
				id: id,
				mid: mid
			};
			if (index == 0 ) {
				All_data.city = city;
			}else {
				All_data.brd = brd
			}
		this.res = res;
		this.zb_title = zb_title;
		this.index = index;
		this.bd_index(url, All_data);
	},
	methods: {
		time_click(i) {
			// i 0 是前一天  1  是 后一天
			var th_time = this.time;
			var { id, mid, index ,city,brd} = this.res;
			var date = [];
			var url = this.baseURL;
			if (i == 1) {
				th_time = moment(th_time)
					.subtract(-1, 'days')
					.format('YYYY-MM-DD');
			} else {
				th_time = moment(th_time)
					.subtract(1, 'days')
					.format('YYYY-MM-DD');
			}

			let All_data = {};
				url = '/topprogram/movie-maincinema';
				All_data = {
					id: id,
					mid: mid,
					bdate: th_time
				};
			if (index == 0) {
				All_data.city = city;
			}else {
				All_data.brd = brd;
			}
			date[0] = new Date(th_time);
			this.date = date;
			this.time = th_time;
			this.bd_index(url, All_data);
		},
		ei_calendar_tap(e) {
			var time = e.format.split('/');
			var th_time = `${time[0]}-${time[1]}-${time[2]}`;
			var url = this.baseURL;
			var { id, mid, index ,city,brd} = this.res;
			var date = [];
			let cityBrd = '';
			
			let All_data = {};
			url = '/topprogram/movie-maincinema';
			All_data = {
				mid: mid,
				bdate: th_time,
				id: id,
			};
			if (index == 0) {
				All_data.city = city;
			}else{
				All_data.brd =brd
			}
			date[0] = new Date(th_time);
			this.date = date;
			this.time = th_time;
			this.bd_index(url, All_data);
			this.time_tab_Br = false;
		},
		async bd_index(url, All_data) {
			let userType = await this.detection_validity();
			All_data.userType = userType;
			let { data: res } = await this.$http.get({
				url,
				data: All_data
			});
			console.log(res);
			var index = this.index;
			// return ''
			
			var data = false;
			var title = '';
			var sum = '';
			data = res.data;
			sum = res.sum;
			title = '影院票房';
			uni.setNavigationBarTitle({
				title
			});
			// gdbo_rate黄金场票房占比  影投票房 是brd
			data.forEach((item, index) => {
				let feneDate = res.fene[item['cinema_code']];
				// 票房占比
				item.bo_rate = (Number(item.total_bo_s)  /( feneDate.total_bo_s - 0) *100).toFixed(2)
				// 排场占比
				item.session_rate = ((item.session_s - 0)  / (feneDate.session_s - 0) *100).toFixed(2)
				// 平均票价
				item.avg_price = Number(item.avg_price).toFixed(2);
				// 票房占比
				// item.bo_rate = (Number(item.bo_rate) * 100).toFixed(2);
				// 上座率
				item.occupancy = (Number(item.occupancy) * 100).toFixed(2);
				// 场均人次
				item.people_per_session = Number(item.people_per_session).toFixed(0);
				// 排场占比
				// item.session_rate = (Number(item.session_rate) * 100).toFixed(2);
				// 场次数 item.session_s
				// 今日票房
				item.total_bo_s = (Number(item.total_bo_s) / 10000).toFixed(2);
				// 人次total_people_s
				// 排座占比
				item.seat_s_zb = (Number(item.seat_s) / sum.seat_s).toFixed(2) + '%';
			});
			if (Number(sum.total_bo_s) / 100000000 > 1) {
				sum.total_bo_s = (Number(sum.total_bo_s) / 100000000).toFixed(2) + '亿';
			} else {
				sum.total_bo_s = (Number(sum.total_bo_s) / 10000).toFixed(2) + '万';
			}
			this.sum_data = sum;
			this.itemlist = data;
		},
		show_index() {
			this.is_index = false;
		},
		more_index_data_tap(index) {
			if (index == 8) {
				return false;
			}
			// console.log(data);
			// // 复制过来一份进行操作
			let zb_title = this.zb_title;
			// 如果点击当前的 按钮是 true  则 变为false
			if (zb_title[index].front_row) {
				zb_title[index].front_row = false;
				// console.log(zb_title[index].front_rowz);
			} else {
				let boer = 0;
				for (let i = 0; i < zb_title.length - 1; i++) {
					if (zb_title[i].front_row) {
						if (boer == 0) {
							boer = 1;
						} else if (boer == 1) {
							boer = 2;
						} else if (boer == 2) {
							boer = 3;
						} else if (boer == 3) {
							boer = 4;
						}
						// boer++;
						// console.log(boer);
					}
					if (boer == 4) {
						uni.showToast({
							title: '最多选择四项',
							duration: 2000,
							icon: 'none'
						});
						return false;
					}
				}
				// console.log(boer);

				if (boer <= 4) {
					zb_title[index].front_row = true;
				}
			}
			// 定义变量 来计算 是否超过 规定数量

			this.zb_title = zb_title;
		},
		disabledDate(date) {
			const start = new Date('2019/5/10').getTime();
			const end = new Date('2019/7/21').getTime();
			return date.getTime() <= start || date.getTime() >= end;
		}
	}
};
